@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@1,300&display=swap);
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Titillium Web', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sectionHead{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    min-height: 30px;
    font-size: 14px;
    padding-left: 10px;
    padding-right: 10px;
}

    .header{
        font-weight: bold;
    }

    .hr{
        flex: 1 1;
        height: 1px;
        margin-top: 15px;
        background-color: gray;
        margin-left: 10px;
    }
.smallTalkContainer{
    width: 100%;
    max-height: 150px;
    padding: 1px 10px 10px 10px;
    overflow: hidden;
}

    .smallTalkTitle{
        margin-bottom: 4px;
        width: 100%;
    }
.smallTalkGroupContainer{
    width: 100%;
}

    .smallTalkGroupFlex{
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        justify-content: space-between;
    }

        .smallTalkGroupFlexItem{
            flex:0 1 32.667%;
            margin-bottom: 4px;
        }

        @media only screen and (max-width: 768px){
            .smallTalkGroupFlex{
                display: grid;
                grid-template-columns:100%;
                width: 100%;
            }

            .smallTalkGroupFlexItem{
                grid-column-start: 1;
                grid-column-end: 2;
            }
        }
.imgTextContainer{
    width: 100%;
    max-height: 400px;
}

    .imgTextPicture{
        width: 100%;
        height: 250px;
    }

        .imgTextImg{
            width: 100%;
            height: 100%;
        }
.imgTextGroupContainer{
    width: 100%;
}

    .imgTextGroupFlex{
        padding-left: 10px;
        padding-right: 10px;
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        justify-content: space-between;
    }

        .imgTextGroupFlexItems{
            flex:0 1 24.25%;
            margin-bottom: 4px;
        }


        @media only screen and (max-width: 768px) {
            .imgTextGroupFlex{
                padding-left: 10px;
                padding-right: 10px;
                display: grid;
                grid-template-columns:100%;
                width: 100%;
            }

            .imgTextGroupFlexItems{
                grid-column-start: 1;
                grid-column-end: 2;
            }
        }
.bigImageGroupContainer{
    display: grid;
    grid-template-columns: 50% 50%;
    width: 100%;
    grid-column-gap: 1%;
    -webkit-column-gap: 1%;
            column-gap: 1%;
}

    .bigImageGroupContainerOne{
        grid-column-start: 1;
        grid-column-end: 2;
    }

    .bigImageGroupContainerTwo{
        grid-column-start: 2;
        grid-column-end: 3;
    }

    @media only screen and (max-width: 768px){
        .bigImageGroupContainerOne{
            grid-column-start: 1;
            grid-column-end: 3;
        }  
        .bigImageGroupContainerTwo{
            grid-column-start: 1;
            grid-column-end: 3;
        }
    }
.BigScreenContainer{
    width: 100%;
    min-height: 500px;
    border: solid thistle;
}

    .BigScreenImage{
        width: 100%;
        height: 500px;
    }
.sideSmallTalkContainer{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: space-between;
}


        .sideSmallTalkFlexItem{
            flex:0 1 49.5%;
            margin-bottom: 4px;
        }
main{
  padding: 0 4%;
}



.navigationContainer{
    background-color: black;
    color: white;
    display: grid;
    grid-template-columns: 10% 60% auto;
    min-height: 45px;
}

    .headers{
        padding: 5px;
    }

    .listContainer{
        grid-column-start: 2;
        grid-column-end: 3;
        height: 100%;
        font-size: 14px;
    }

        .unordered{
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            list-style: none;
            flex-wrap: wrap;
            height: 100%;
        }

            .unordered li{
                flex: 1 1;
                text-align: center;
                height: 100%;
                padding-top:11px;
                padding-bottom: 11px;
            }

                .linkStyle{
                    text-decoration: none;
                    color: white;
                }

    .searchContainer{
        grid-column-start: 3;
        grid-column-end: 4;
        padding: 7px;
    }

        .searchDiv{
            border: solid white 2px;
            float: right;
            color: white;
            height: 30px;
            border-radius: 20px;
            padding-left: 8px;
            padding-top: 5px;
            padding-bottom: 5px;
            padding-right: 5px;
        }

            .searchBox{
                height: 100%;
                border: none;
                float: left;
                background-color: transparent;
                color: white;
            }

            .searchBox[placeholder="search"]{
                color: white;
            }

            .searchDiv button{
                background-color: transparent;
                height: 100%;
                float: left;
                border: 0px;
            }

            .searchDiv button img{
                height: 100%;
                float: left;
                color: rgb(255, 255, 255);
            }

.articleContainer{
    padding:15px;
}

.articleImgDiv{
    width:50%;
    min-height:500px;
    margin: 0 auto;
}

    .articleImg{
        width:100%;
        height:500px;
    }

    .articleArt{
        width: 80%;
        margin: 0 auto;
    }
.footerContainer{
    background-color: black;
    text-align: center;
}

    .footerPara{
        color: white;
        font-weight: bold;
    }
